.feedback-container {
    max-width: 500px;
    padding: 20px; /* Corrected padding */
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(34, 32, 32, 0.1); /* Enhanced shadow */
    background-color: #ffffff3a;
    margin: 0 auto; /* Center the container */
  }
  
  .feedback-form {
    display: flex;
    flex-direction: column;
  }
  
  .feedback-form .form-group {
    margin-bottom: 15px; /* Increased space between form groups */
  }
  
  .feedback-form label {
    font-weight: bold;
    margin-bottom: 8px; /* Adjusted space below labels */
    display: block;
    font-size: 14px;
    color: #333;
  }
  
  .feedback-form input,
  .feedback-form textarea {
    width: 100%;
    padding: 12px; /* Slightly increased padding for better usability */
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
    box-sizing: border-box; /* Ensure padding is included in width */
  }
  
  .feedback-form textarea {
    resize: vertical;
    min-height: 120px; /* Increased minimum height for better user experience */
  }
  
  .feedback-form button {
    padding: 12px 25px; /* Increased padding for a more substantial button */
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease; /* Smooth transition on hover */
  }
  
  .feedback-form button:hover {
    background-color: #0056b3;
  }
  .back-to-home-button {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
    margin-top: 20px;
}

.back-to-home-button:hover {
    background-color: #45a049;
}
